.number-panel-wrapper {
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  width: 230px;
  margin-left: auto;
  margin-right: 20px;
  margin-bottom: 20px;
  color: #4a4a4a;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  justify-content: flex-start;
  padding: 10px;
}

.title {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  color: #4a4a4a;
  margin-bottom: 5px;
}

.number-panel-text {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  display: block;
  margin-bottom: 5px;
}

.number-panel__inputs-block {
  display: flex;
  margin-top: 15px;
}

.radiobox {
  flex: 1 1 auto;
  display: flex;
}

.radiobox:hover,
.radiobox__label:hover {
  cursor: pointer;
}

.radiobox__input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.radiobox__label {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  border: 1px solid #dfe5ec;
  background: #ffffff;
  color: #4a4a4a;
  flex: 1 1 auto;
}

.radiobox input:checked + .radiobox__label {
  color: #ffffff;
  background: #2196f3;
  border: 1px solid #2196f3;
}

.first-child {
  border-radius: 5px 0 0 5px;
}

.last-child {
  border-radius: 0 5px 5px 0;
}
