@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
}

/* Сброс стилей у button */
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

body {
  background: #f3f7fa;
  overflow-y: scroll;
}

.app {
  background: #f3f7fa;
  height: 100%;
}

.main {
  display: flex;
  text-align: center;
  padding-bottom: 20px;
}

.left-aside {
  flex: 1 1 auto;
  align-self: flex-start;
  min-width: 230px;
  height: 100%;
}

.right-aside {
  min-width: 500px;
  flex: 1 1 auto;
  align-self: flex-start;
  height: 100%;
}

.header-wrapper {
