.inputs-block {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  width: 230px;
  margin-left: auto;
  margin-right: 20px;
  margin-bottom: 20px;
  color: #4a4a4a;
  align-content: flex-start;
}

.checkbox-heading {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  margin: 20px;
  text-transform: uppercase;
  text-align: left;
  color: #4a4a4a;
}

.checkbox {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  display: block;
  text-align: left;
  padding-left: 50px;
  margin-bottom: 20px;
  width: 100%;
}

.checkbox:hover,
.checkbox label:hover,
.checkbox__input:hover {
  background: #f1fcff;
  cursor: pointer;
}

.checkbox__input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.checkbox__input:checked + .checkbox__box {
  border: 1px solid #2196f3;
  background-image: url(data:image/png;base64,);
  background-size: 12 auto;
  background-repeat: no-repeat;
  background-position: center;
}

.checkbox__box {
  position: absolute;
  margin-left: -30px;
  width: 20px;
  height: 20px;
  border: 1px solid #9abbce;
  box-sizing: border-box;
  border-radius: 2px;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}

